
.secondary-nav {
  text-align: center;
  background-color: white;
  border-bottom: 1px solid #e0e0e0;
  position: relative;
}
.secondary-nav .ui.secondary.pointing.menu {
  margin: 0 auto;
  border-bottom-color: transparent;
  height: 40px;
  font-size: 13px;
}
.secondary-nav .ui.secondary.pointing.menu .item {
  color: #888;
}
.secondary-nav .lesson-action {
  height: 40px;
  float: right;
}



